.post{
    max-width: 600px;
    background-color: #fff;
    border: 1px solid lightgray;
    border-radius: 5px;
    margin-bottom: 10px;
}
.post_image {
  width: 100%;
  object-fit: contain;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
}
.post_h3_1 {
  font-weight: normal;
  padding: 10px 20px;
}
.post_header {
  display: flex;
  align-items: center;
  padding: 20px;
}
.post_avatar {
  margin-right: 10px;
}
.post_commentBox{
  display: flex;
  margin-top: 5px;
}
.post_input{
  flex: 1;
  
}
.post_comments{
  padding:20px;
}
.post_button{
  
  flex:0;
}