*{
  margin: 0px;
}
.App{
  background-color: #FAFAFA;
  height: 100%;
}
.app_header{
  z-index: 1;
  position: sticky;
  top : 0;
  background-color: white;
  object-fit: contain;
  padding: 20px;
  border-bottom: 1px solid lightgray;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  
}
.app_signup{
  display: flex;
  flex-direction: column;
}
.app_headerImage{
  object-fit: contain;
}
.app_posts{
  display: flex;
  justify-content: center;
}
